@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@0,400;0,500;0,600;0,700;1,400;1,500;1,600;1,700&display=swap);
* {
  font-family: "Roboto Mono", monospace;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --red: #c10000;
  --teal: #00989d;
  --pink: #ff65c3;
  --dark-red: #7c1a0f;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
nav {
  margin: 0;
  padding: 0;
}

* {
}

.navigation-bar {
  padding: 1% 2%;
  position: fixed;
  width: 100vw;
  top: 0;
  z-index: 10;
  background-color: transparent;
}

.navigation-bar.colourChange {
  background-color: #f8f9fa;
  box-shadow: 0px 15px 10px -15px rgb(134, 134, 134);
}

.navbar-toggler {
  color: rgba(0, 0, 0, 0.55);
}

.dropdown-nav {
  justify-content: flex-end;
}

.logo {
  max-height: 5rem;
}

.nav-links {
  display: flex;
}

.my-link {
  font-size: 1.2rem;
  display: flex;
  justify-content: flex-end;
  text-decoration: none;
  color: rgba(0, 0, 0, 0.55);
  margin: 0 1rem;
}

.my-link:hover {
  border-bottom: 2px solid;
  color: var(--red) !important;
}

.my-link:focus {
  border-bottom: 2px solid;
  color: var(--red) !important;
}

.active {
  border-bottom: 2px solid;
  color: var(--red) !important;
}

.hero {
  height: 33rem;
  width: 100vw;
  background: url(/latinmotion/static/media/latinmotion-banner.7c31cc12.png);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  text-align: center;
  margin-top: 7rem;
}

.section-container {
  margin: 0rem auto;
  box-sizing: border-box;
}

.about-content {
  text-align: center;
}

.about-text {
  font-size: 1.2rem;
  color: rgba(0, 0, 0, 0.55);
}

.about-img {
  height: 14rem;
  width: 20rem;
}

.carousel-div {
  margin: 2rem 0;
}

@media screen and (min-width: 1400px) {
  .section-container {
    width: 70%;
  }
}

@media screen and (min-width: 768px) {
  .about-text {
    padding: 1rem 0 0 2rem;
  }
}

@media screen and (max-width: 767px) {
  .about-text {
    width: 90%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 2rem;
    text-align: center;
  }
}

.section-title {
  font-size: 2.5rem;
  text-align: center;
  font-weight: bold;
  margin: 2rem 0rem;
  color: rgba(0, 0, 0, 0.55);
}

.flex-container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  max-width: 100vw;
}

.flex-container--padded {
  padding-top: 2rem;
}

.flex-container--centered {
  justify-content: center;
}

.flex-container--grey {
  background-color: #f7f7f7;
}

@media screen and (min-width: 1000px) {
  .flex-container {
    padding-bottom: 2rem;
  }
}

.class-cards-container {
  margin: 0 3rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.venue-card {
  min-width: 26vw;
  max-width: 28vw;
  height: 15rem;
  padding: 2rem;
  background-color: var(--teal);
  margin: 0 2rem;
  border-radius: 10%;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.12), 0 4px 8px rgba(0, 0, 0, 0.06);
}

.venue-name {
  font-size: 1.5rem;
  font-weight: bold;
  color: white;
}

.view-btn {
  background-color: transparent;
  border: 5px solid white;
  color: white;
  width: 50%;
  height: 3rem;
}

.venue-card:hover {
  -webkit-transform: scale(1.01);
          transform: scale(1.01);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.3), 0 4px 8px rgba(0, 0, 0, 0.2);
}


